.user{
    width: 100%;
    height: 25%;
    display: flex;
    flex-direction: column;
    align-items: center;
    margin-bottom: 2rem;
}

.user > .user__figure{
    width: 10rem;
    height: 10rem;
    object-fit: cover;
    object-position: center;
    -o-object-fit: cover;
    -o-object-position: center;
    margin-bottom: 5%;
}

#twitter {
    font-size: 1.8rem;
    font-weight: bold;
}

/**************************************************************
                            Media Quary
**************************************************************/

@media only screen and (min-width: 768px)  {
    
    .user{
        margin-bottom: 3rem;
    }

    .user > .user__figure{
        margin-bottom: 2rem;
    }
}
